@charset "UTF-8";

html, body {
    height: 100%;
}

html body {
    background-color: #f5f5f5;
    background-image: url("/images/pattern-light-grey.svg");
    font-size: 0.875rem;
}

main {
    padding-top: 70px;
}

html caption {
    caption-side: top;
}

.wrap {
    min-height: 100%;
    height: auto;
    margin: 0 auto -77px;
    padding: 0 0 77px;
}

.u-update-btn {
    text-decoration: underline;
    color: #777;
}

.bg-white {
    padding: 0.5rem;
    border-radius: 4px;
    box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.24);
}

.table th a.asc::after {
    content: "";
    font-family: "Font Awesome 5 Free";
    margin-left: 10px;
}

.table th a.desc::after {
    content: "";
    font-family: "Font Awesome 5 Free";
    margin-left: 10px;
}

span.not-set {
    color: #dc3545;
}

@media (min-width: 1200px) {
    .bd-links.collapse,
.bd-links .collapse {
        display: block !important;
    }

    .modal-lg, .modal-xl {
        max-width: 1000px;
    }
}

/** Редактирование изображения профиля */

.rs-pencil {
    width: 30px;
    cursor: pointer;
    position: absolute;
    right: 0;
    bottom: 30px;
    background-color: #fff;
    text-align: center;
    color: #007bff;
}

.rs-pencil::before {
    color: #007bff;
}

.rs-pencil span {
    line-height: normal;
    width: 100%;
}

.edit-photo-block {
    background-color: #fff;
}

.edit-photo-block .help-block {
    position: absolute;
    top: 0;
    background-color: #fff;
}

.edit-photo-block .help-block,
.edit-photo-block .form-group {
    margin: 0;
}

html .add-photo-input {
    display: none;
}

.av-block {
    display: inline-block;
}

.av-preview {
    position: relative;
    min-width: 200px;
    min-height: 200px;
}

.av-preview img {
    height: 200px;
    width: 200px;
}

/* Календарь */

.ui-widget-header {
    background: #fff;
    color: #007bff;
    border-color: #007bff;
    font-weight: 400;
}

.ui-widget-content a {
    background-color: #fff;
}

.ui-widget-header .ui-icon {
    background-image: none;
    font-size: 0;
}

.ui-widget-header .ui-icon::before, .ui-widget-header .ui-icon::after {
    content: "";
    display: block;
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: #000;
    top: 40%;
    left: 3px;
}

.ui-widget-header .ui-icon-circle-triangle-e::before {
    transform: rotate(45deg);
}

.ui-widget-header .ui-icon-circle-triangle-e::after {
    transform: rotate(-45deg);
    top: 60%;
}

.ui-widget-header .ui-icon-circle-triangle-w::before {
    transform: rotate(-45deg);
}

.ui-widget-header .ui-icon-circle-triangle-w::after {
    transform: rotate(45deg);
    top: 60%;
}

.ui-state-hover,
.ui-widget-content .ui-state-hover,
.ui-widget-header .ui-state-hover,
.ui-state-focus,
.ui-widget-content .ui-state-focus,
.ui-widget-header .ui-state-focus,
.ui-button:hover,
.ui-button:focus {
    background: #007bff;
    color: #fff;
    top: 2px;
    cursor: pointer;
    border: none;
}

.ui-state-hover .ui-icon::before, .ui-state-hover .ui-icon::after,
.ui-widget-content .ui-state-hover .ui-icon::before,
.ui-widget-content .ui-state-hover .ui-icon::after,
.ui-widget-header .ui-state-hover .ui-icon::before,
.ui-widget-header .ui-state-hover .ui-icon::after,
.ui-state-focus .ui-icon::before,
.ui-state-focus .ui-icon::after,
.ui-widget-content .ui-state-focus .ui-icon::before,
.ui-widget-content .ui-state-focus .ui-icon::after,
.ui-widget-header .ui-state-focus .ui-icon::before,
.ui-widget-header .ui-state-focus .ui-icon::after,
.ui-button:hover .ui-icon::before,
.ui-button:hover .ui-icon::after,
.ui-button:focus .ui-icon::before,
.ui-button:focus .ui-icon::after {
    background-color: #fff !important;
}

.bd-placeholder-img {
    text-anchor: middle;
}

html .modal-backdrop.show {
    opacity: 0.1;
}

/*Конец стилей с асфальта*/

a {
    color: #fe17bf;
}

.navbar-brand {
    color: #9d9d9d;
}

.navbar-brand:hover {
    color: #fe17bf;
}

.rs-navbar {
    border-bottom: 1px solid #fe17bf;
    background-color: #fff;
    padding: 0.3rem 1rem;
}

.nav-item a {
    color: #9d9d9d;
}

.nav-item a:hover, .nav-item a.active {
    color: #fe17bf;
}

.nav-item a.active {
    cursor: default;
}

.navbar-toggler {
    border: 1px solid #fe17bf;
    outline: none;
}

.navbar-toggler:active, .navbar-toggler:focus {
    outline: none;
}

.navbar-toggler .navbar-toggler-icon {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    height: auto;
    color: #fe17bf;
    transition: 0.3s;
    transform: rotate(-90deg);
}

.navbar-toggler .navbar-toggler-icon::before {
    content: "";
}

.navbar-toggler[aria-expanded=false] .navbar-toggler-icon {
    transform: rotate(0);
}

nav.sidebar {
    padding-top: 70px;
    min-width: 200px;
}

.sidebar-sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 70px;
    padding-top: 0.5rem;
    overflow-x: hidden;
    overflow-y: auto;
}
